.hero-holder {
    .features-page & {
        background:$text-color-primary url('../images/hero/hero-features.jpg') no-repeat 50% 50%;
        @include background-size(cover);
    }
}

.feature-blocks {
    padding-top: 30px;
    padding-bottom: 90px;
    .feature-block {
        padding-top: 60px;
        padding-bottom: 60px;
    }
    .feature-title {
        color: $text-color-primary;
        margin-top: 0;
        margin-bottom: 15px;
    }
    .feature-desc {
        padding-right: 30px;
        font-size: 14px;
    }
    
    .figure-holder img{
        
    }
}




